import * as THREE from 'three';
import * as BufferGeometryUtils from 'three/examples/jsm/utils/BufferGeometryUtils';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls';

import * as CANNON from 'cannon-es';
import CannonManager from './CannonManager';

import CannonDebugger from 'cannon-es-debugger';

import data from "../data.json";
import { GLTFLoader } from 'three-stdlib';
import { clamp, easeInOut, lerp, lerp3, toConvexProps } from '../utils';


class ThreeManager{
    constructor(canvas, reactFunctions, canvasManager){
        this.scene = new THREE.Scene();
        this.camera = new THREE.PerspectiveCamera( 30, window.innerWidth / window.innerHeight, 0.1, 1000 );
        this.renderer = new THREE.WebGLRenderer({canvas: canvas, alpha: true, antialias: true});
        
        this.canvasManager = canvasManager;
        this.cannonManager = new CannonManager();

        this.titleActive = true;
        this.inTitleTransition = true;

        this.numLoaded = 0;
        this.numObjects = data.objects.length;
        this.allLoaded = false;

        this.reactFunctions = reactFunctions;

        this.objects = [];

        this.selectedObject = undefined;

        this.phys = this.cannonManager;

        this.mouse = undefined;

        this.forceHoverTo = undefined;
        this.hoverObjProjPos = undefined;

        this.init();
    }

    init(){

        this.camera.custom = {
            startPos: {
                wide: new THREE.Vector3(0,0,13),
                narrow: new THREE.Vector3(0,0,13)
            },
            endPos: {
                wide: new THREE.Vector3(3,-3.3,13),
                narrow: new THREE.Vector3(-3.7,-3.7,15)
            },
            titlePos: {
                wide: new THREE.Vector3(-0.55,-0.3,5),
                narrow: new THREE.Vector3(-0.8,-0.7,5)
            },
            startFov: 25,
            endFov: 45,
            t: 0,
            update: () => {
                let cust = this.camera.custom;

                if(cust.focus | this.titleActive){
                    cust.t += 0.0015
                } else {
                    cust.t -= 0.0015
                }



                if(!this.active && cust.t <= 0){

                    this.inTitleTransition = false;
                }

                cust.t = clamp(cust.t,0,1);

                let tEase = easeInOut(cust.t); 
                let st; let end;
                
                if(this.canvasManager.w > 800){
                    
                    if(this.inTitleTransition){
                        end = {...cust.titlePos.wide};
                        st = cust.startPos.wide;
                    } else {

                        st = cust.startPos.wide;
                        end = {...cust.endPos.wide};

                        // changes made to endPos for better adaptability to screen size
                        end.x = end.x - (this.canvasManager.w-800)*0.001;
                        end.y = end.y + (this.canvasManager.w-800)*0.003;
                        end.z = end.z - (this.canvasManager.w-800)*0.01;


                    }


                } else {
                    if(this.inTitleTransition){
                        end = cust.titlePos.narrow;
                        st = cust.startPos.narrow;
                    } else {
                        st = cust.startPos.narrow;
                        end = cust.endPos.narrow;
                    }
                    
                }
                
                let {x,y,z} = lerp3(st,end,tEase);

                this.camera.position.set(x,y,z);

                this.camera.fov = lerp(cust.startFov, cust.endFov, tEase);
                this.camera.updateProjectionMatrix();


            },
            focus: false
        }

        this.renderer.setClearColor(0x000000,0);

        this.renderer.shadowMap.enabled = true;
        this.renderer.shadowMap.type = THREE.PCFShadowMap;
        this.renderer.shadowMap.mapSize = new THREE.Vector2(1024,1024)


        const geometry = new THREE.BoxGeometry( 1, 1, 1 );
        const material = new THREE.MeshPhongMaterial( { color: 0xff0000 } );


        // this.controls = new OrbitControls(this.camera,this.renderer.domElement);
        this.buildWorld()

        this.camera.position.set(0,0,15);
        this.debug = new CannonDebugger(this.scene,this.phys.world,{color: "red"});


        this.loadModels();

        this.animate();
    }

    setTitleActive(state){
        this.titleActive = state;

        if(state){
            this.inTitleTransition = true;
        }
    }

    buildWorld(){

        const ambient = new THREE.AmbientLight(0xffffff,0.5);
        this.scene.add(ambient);

        const sun = new THREE.DirectionalLight(0xffffff,4);
        sun.lookAt(new THREE.Vector3(0,3,0));
        sun.position.set(15,2,15);
        sun.castShadow = true;
        sun.shadow.camera.top = 2;
        sun.shadow.camera.bottom = -2;
        sun.shadow.camera.left = -2;
        sun.shadow.camera.right = 2;


        this.scene.add(sun);


    }

    loadModels(){
        const loader = new GLTFLoader();
        console.log(data);
        data.objects.forEach((obj,i) => {
            loader.load(`./assets/${obj.fileName}`,(gltf) => {

                let children = gltf.scene.children
                let mainMesh = children[0];
                mainMesh.castShadow = true;
                mainMesh.receiveShadow = true;

                let originalMaterial = mainMesh.material.clone();
                let blankMaterial = mainMesh.material.clone();
                // let blankMaterial = new THREE.MeshPhongMaterial({color: "#045c11", specular: 1, shininess: 0.5});
                mainMesh.material = blankMaterial;

                // mainMesh.material.side = THREE.DoubleSide;

                let parent = this;
                let phys = this.phys;

                this.numLoaded++;

                if(this.numLoaded == this.numObjects){
                    this.allLoaded = true;
                    this.reactFunctions.setLoading(false);
                } else {
                    console.log("not all")
                }

                this.reactFunctions.setLoadPercent(this.numLoaded/this.numObjects);


                let colliderShape;

                if(obj.collider == "cylinder"){
                    colliderShape = new CANNON.Cylinder(...obj.col_args)
                } else if(obj.collider == "box"){
                    colliderShape = new CANNON.Box(new CANNON.Vec3(...obj.col_args))
                }

                let body = new CANNON.Body({mass:1,shape:colliderShape});
                body.position.set((i*2)-data.objects.length,2,2);
                this.phys.world.addBody(body);

                mainMesh.body = body;

                let p = this.phys.constraint.vec
                let bodyB = new CANNON.Body({position: p});
                let distConstraint = new CANNON.DistanceConstraint(mainMesh.body, bodyB,0.3,0.5);
                // distConstraint.disable();
                this.phys.world.addConstraint(distConstraint);

                this.objects.push(mainMesh);
                this.scene.add(mainMesh);

                mainMesh.custom = {
                    index: obj.index,
                    mesh: mainMesh,
                    parent: parent,
                    phys: phys,
                    selected: false,
                    hover: false,
                    constraint: distConstraint,
                    originalMaterial: originalMaterial,
                    blankMaterial: blankMaterial,
                    startTransition: () => {
                        let b = mainMesh.body;
                        mainMesh.custom.startPos = {
                           x: b.position.x, 
                           y: b.position.y, 
                           z: b.position.z 
                        }

                        let euler = new CANNON.Vec3(4,4,4);

                        mainMesh.body.quaternion.toEuler(euler);
                        console.log(euler);

                        mainMesh.custom.startRot = {
                            x: euler.x, y: euler.y, z: euler.z
                        }

                        mainMesh.material = mainMesh.custom.originalMaterial;
                        mainMesh.custom.constraint.disable();

                    },
                    deselect: () => {
                        mainMesh.material = mainMesh.custom.blankMaterial;
                        mainMesh.custom.selected = false;
                        mainMesh.custom.t = 0;
                        mainMesh.custom.constraint.enable();
                    },
                    update: () => {

                        if(mainMesh.custom.selected){

                            mainMesh.custom.t += 0.01;

                            let st = mainMesh.custom.startPos;

                            let end;
                            if(this.canvasManager.w > 800){

                                end = {...mainMesh.custom.endPos.wide};

                                // changes made to endPos for better adaptability to screen size

                                end.x = end.x - (this.canvasManager.w-800)*0.001;
                                end.y = end.y + (this.canvasManager.w-800)*0.003;
                                end.z = end.z - (this.canvasManager.w-800)*0.009;

                            } else {
                                end = mainMesh.custom.endPos.narrow;
                            }


                            let tClamp = clamp(mainMesh.custom.t,0,1)
                            let tEase = easeInOut(tClamp);    
                            let {x,y,z} = lerp3(st,end,tEase);

                            mainMesh.body.position.set(x,y,z);

                            let endR = {x:0.3,y:mainMesh.custom.t*1.5,z:0}

                            let rotVal = lerp3(mainMesh.custom.startRot, endR, tEase);
                            
                            mainMesh.body.quaternion.setFromEuler(rotVal.x, rotVal.y, rotVal.z, "XYZ");

                        } else {

                            let dist = mainMesh.body.position.distanceTo(this.phys.constraint.vec);
                            let velClamp = (dist * 2);

                            mainMesh.body.velocity.x = clamp(mainMesh.body.velocity.x,-velClamp,velClamp);
                            mainMesh.body.velocity.y = clamp(mainMesh.body.velocity.y,-velClamp,velClamp);
                            mainMesh.body.velocity.z = clamp(mainMesh.body.velocity.z,-velClamp,velClamp);
                        }

                        mainMesh.custom.hoverBehavior();
        
                    },
                    hoverBehavior: () => {

                        if(mainMesh.custom.index == this.forceHoverTo){
                            mainMesh.custom.hover = true;
                        }

                        //changes material if hovered
                        if(mainMesh.custom.hover && !mainMesh.custom.selected){
                            let newMat = mainMesh.custom.originalMaterial.clone();
                            newMat.emissive = new THREE.Color(0x8C3575);
                            newMat.emissiveIntensity = 1;
                            mainMesh.material = newMat;
                        } else {
                            mainMesh.material = mainMesh.custom.originalMaterial;
                        }


                        if(mainMesh.custom.hover){
                            let projectionPos = mainMesh.position.project(this.camera);
                            
                            let onScreen = projectionPos.clone();
                            onScreen.x = ( onScreen.x + 1) * this.canvasManager.w / 2;
                            onScreen.y = - ( onScreen.y - 1) * this.canvasManager.h / 2; 
                            this.hoverObjProjPos = onScreen;
                        }
                        
                        mainMesh.custom.hover = false;

                    },
                    constrain: () => {
                        // const distConstraint = new CANNON.DistanceConstraint(this.selectedObject.body, this.phys.constraint.body,0,10);

                        let c = this.phys.constraint;
                        let bodyA = new CANNON.Body({position: new CANNON.Vec3(c.x,c.y,c.z)});

                        

                        // this.phys.world.addBody(bodyA);

                        mainMesh.custom.pointConstraint = new CANNON.PointToPointConstraint(this.selectedObject.body,this.phys.constraint.vec, bodyA, this.phys.constraint.vec2,1);

                        this.phys.world.addConstraint(mainMesh.custom.pointConstraint);
                        // this.phys.world.addConstraint(distConstraint);
                    },
                    deConstrain: () => {
                        this.phys.world.removeConstraint(mainMesh.custom.pointConstraint)
                    },  
                    startRot: {x:0,y:0,z:0},
                    endRot: {x:1,y:0,z:0},
                    startPos: {x:3,y:3,z:3},
                    endPos: {
                        narrow:{x: -3.7, y: -3.5, z: 12},
                        wide: {x: 3.5, y: -3.5, z: 10}
                    },
                    t: 0
                }

            })
        })

    }

    animate(){
        requestAnimationFrame(this.animate.bind(this));

        if(this.allLoaded){
            this.phys.world.fixedStep();
            
            this.objects.forEach(obj => {
                this.camera.custom.update();
                
                if(obj.custom){
                    obj.custom.update();
                }
                
                obj.position.copy(obj.body.position);
                obj.quaternion.copy(obj.body.quaternion);
            })
            
            this.checkHover();
            // this.debug.update();
            
            this.renderer.render(this.scene, this.camera);

        }

    }


    select(e,w,h){
        let mouseVector = new THREE.Vector2();
        mouseVector.x = 2 * (e.clientX / w)-1;
        mouseVector.y = 1 - 2 * (e.clientY / h);

        console.log(mouseVector.y,this.mouse.y);

        let raycaster = new THREE.Raycaster();
        raycaster.setFromCamera(mouseVector,this.camera);

        let intersects = raycaster.intersectObjects(this.scene.children);
        // console.log(intersects);

        if(intersects.length){

            //if trying to select the current selected, then just deselect
            if(intersects[0].object == this.selectedObject){
                this.deselectObj(intersects[0].object);
            } else {
                this.selectObj(intersects[0].object)
            }


        }
    }

    mousePosUpdate(e){
        let w = this.canvasManager.w;
        let h = this.canvasManager.h;

        let mouseVector = new THREE.Vector2();
        mouseVector.x = 2 * (e.clientX / w)-1;
        mouseVector.y = 1 - 2 * (e.clientY / h);

        // console.log(mouseVector.x,mouseVector.y);
    
        this.mouse = mouseVector; 
    }

    checkHover(){
        if(this.mouse){
            let raycaster = new THREE.Raycaster();
            raycaster.setFromCamera(this.mouse,this.camera);
    
            let intersects = raycaster.intersectObjects(this.scene.children);

            if(intersects.length){
                // console.log(intersects[0]);
                let obj = intersects[0].object
                obj.custom.hover = true;

            }
        }
    }

    forceHover(index){
        this.forceHoverTo = index;
    }

    selectObj(obj){

        this.reactFunctions.setSelected(obj.custom.index);

        if(!obj){
            console.error("No Object");
            return;
        }

        if(this.selectedObject){
            this.selectedObject.custom.deselect();
        }
        // this.selectedObject.custom.deConstrain();
        this.selectedObject = obj;
        this.selectedObject.custom.selected = true;
        this.selectedObject.custom.startTransition();

        this.camera.custom.focus = true;
    }

    deselect(){
        if(this.selectedObject){
            this.selectedObject.custom.deselect();
        }

        this.camera.custom.focus = false;
    }

    deselectObj(obj){

        this.reactFunctions.setSelected(undefined);
        this.selectedObject.custom.deselect();
        this.selectedObject = undefined; 
        this.camera.custom.focus = false;
    }

    deselectByIndex(index){
        this.objects.forEach((obj) => {
            if(obj.custom && index == obj.custom.index){
                this.deselectObj(obj);
            }
        })
    }

    setDistances(dist){
        this.objects.forEach((obj,i) => {
            if(obj.custom){
                obj.custom.constraint.distance = dist;
            }
        })
    }
}

export default ThreeManager;