import React, {useEffect, useState} from 'react';

function HoverLine({hover, canvasManager, lineTarget}) {

    const [x1, setX1] = useState(undefined);
    const [y1, setY1] = useState(undefined);
    const [x2, setX2] = useState(undefined);
    const [y2, setY2] = useState(undefined);

    useEffect(() => {
        if(lineTarget && canvasManager && hover != undefined){
            let bounds = lineTarget.getBoundingClientRect();
            setX1(bounds.x);
            setY1(bounds.y);

            console.log(bounds);


            const updateInterval = setInterval(() => {

                if(hover == undefined){
                    clearInterval(updateInterval);
                }

                let projPos = canvasManager.threeManager.hoverObjProjPos;
                if(projPos){
                    setX2(projPos.x);
                    setY2(projPos.y);
                }
            },1000/30)


        } else {
            setX1(undefined);
            setY1(undefined);
            setX2(undefined);
            setY2(undefined);
        }


    },[hover])

    return (
        <svg className='Hover-Line-Cont'>
            {x1 && y1 && x2 && y2 ? 
                <line x1={x1} y1={y1} x2={x2} y2={y2} strokeLinecap='round' strokeWidth={4} stroke='var(--green-light)'/> : ""}
            {x1 && y1 && x2 && y2 ? 
                <line x1={x1} y1={y1} x2={x2} y2={y2} stroke='var(--purple)' pathLength={2} /> : ""}
        </svg>
    );
}

export default HoverLine;