import React,{useEffect, useState} from 'react';
import CanvasManager from '../scripts/CanvasManager';

function ThreeCanvas({selected, focused, setSelected, setFocused,canvasManager, info}) {


    // changes when new selected
    useEffect(() => {
        if(canvasManager){
            let objMatch;

            if(selected == undefined){
                canvasManager.threeManager.deselect();
            } else {
                canvasManager.threeManager.objects.forEach((obj) => {
                    if(obj.custom && parseInt(selected) == obj.custom.index){
                        canvasManager.threeManager.selectObj(obj);
                    }
                })
            }

            // canvasManager.threeManager.selectObj()

        }

    },[selected])

    const handleCanvasClick = (e) => {
        canvasManager.click(e);
    }

    const handleMouseMove = (e) => {

        if(canvasManager){
            canvasManager.threeManager.mousePosUpdate(e);
        }
    }


    return (
        <div className='Canvas-Cont'>
            <canvas style={info ? {transform: "translateX(5vw) translateY(5vh)", pointerEvents: "none" }:{}}  onMouseMove={handleMouseMove} onMouseDown={handleCanvasClick} id="three-canvas"></canvas>

        </div>
    );
}

export default ThreeCanvas;