import React from 'react';

function ObjectText({data, active, setSelected}) {
    return (
        <div className={`Text ${active ? "Text-Active":"Text-Inactive"}`}>
            <span className='Overlay-Back' onMouseDown={() => {setSelected(undefined)}}>{"< Back"}</span>
            <h1>{data.title}</h1>
            <p className='Author'>{data.author ? data.author : "Author Person"}</p>
            <p> {data.paragraph ? data.paragraph : "Filler Text, this is a bunch of filler text. Text with no meaning, that's what this one is!"}
                </p>
        </div>
    );
}

export default ObjectText;