import { useEffect, useState } from 'react';
import './App.css';
import ThreeCanvas from './components/ThreeCanvas';
import Selector from './components/Selector';
import CanvasManager from './scripts/CanvasManager';
import ObjectText from './components/ObjectText';

import data from "./data.json";
import Title from './components/Title';
import Loading from './components/Loading';
import MiniTitle from './components/MiniTitle';
import TocRows from './components/TocRows';
import HoverLine from './components/HoverLine';
import Info from './components/Info';

function App() {

    const [selected, setSelected] = useState(undefined);
    const [focused, setFocused] = useState(false);
    const [hover, setHover] = useState(undefined);

    // place on table of contents that the line that connects 3d object to its table off contents entry goes to
    const [lineTarget, setLineTarget] = useState(undefined);

    const [title, setTitle] = useState(true);
    const [loading, setLoading] = useState(true);
    const [loadPercent, setLoadPercent] = useState(0);

    const [info, setInfo] = useState(false);

    const [canvasManager, setCanvasManager] = useState()

    useEffect(() => {
        setCanvasManager(new CanvasManager("#three-canvas",{setSelected: setSelected, setFocused: setFocused, setHover: setHover, setLoading: setLoading, setLoadPercent: setLoadPercent}));
        
    },[])

    useEffect(() => {
        if(canvasManager){
            canvasManager.threeManager.forceHover(hover);
        }
    }, [hover])


    const generateObjectText = () => {

        
        if(selected != undefined){
            let objData = data.objects[selected]
            return <ObjectText data={objData} />
        }
    }

    const handleTitleChange = (state) => {
        setTitle(state);
        canvasManager.setTitleActive(state);
    }

    const handleInfoChange = (state) => {
    
        setInfo(state);
        // setTitle(!state);
    }


    return (
        <div className="App">

            <Title active={title && !loading && !info} setTitle={handleTitleChange} setInfo={handleInfoChange}/>
            <Info active={info} setInfo={handleInfoChange}/>

            {loading ? <Loading loadPercent={loadPercent} /> : ""}
            
            <TocRows align={"Left"} objects={data.objects} start={0} end={5} active={!title && selected == undefined} selected={selected} setSelected={setSelected} setHover={setHover} setLineTarget={setLineTarget}/>
            <TocRows align={"Right"} objects={data.objects} start={6} end={12} active={!title && selected == undefined} selected={selected} setSelected={setSelected} setHover={setHover} setLineTarget={setLineTarget}/>

            <div className={`Overlay ${selected != undefined ? "Fade-Object-Active":"Fade-Object-Inactive"}`}>
                { !title ? data.objects.map((obj,i) => {
                    return ( <ObjectText setSelected={setSelected} key={i} data={obj} active={obj.index == selected}/>)
                }): ""}
            </div>

            <ThreeCanvas selected={selected} setSelected={setSelected} focused={focused} setFocused={setFocused} canvasManager={canvasManager} info={info} />

            <MiniTitle title={title} setTitle={setTitle} />

            {hover != undefined ? 
                <HoverLine hover={hover} canvasManager={canvasManager} lineTarget={lineTarget} /> : ""
            }

            <div className={`ReadMore ${!title ? "Fade-Object-Active":"Fade-Object-Inactive"}`}>
                <div>Read More Here!</div>
            </div>

            {/* {title ? "" :
                <Selector selected={selected} setSelected={setSelected} focused={focused} setFocused={setFocused} canvasManager={canvasManager} />
            } */}

        </div>
    );
}

export default App;
