import React from 'react';

function TocRows({active, objects, start=0, end=1000, align, selected, setSelected, setHover, setLineTarget}) {

    const handleMouseDown = (e) => {
        let index = e.currentTarget.dataset.index;
        setSelected(parseInt(index));
    }

    const mouseEnter = (e) => {
        let index = e.currentTarget.dataset.index;
        setHover(parseInt(index));
        setLineTarget(e.currentTarget.lastChild);

    }

    const mouseLeave = (e) => {
        setHover(undefined);
        setLineTarget(undefined);
    }


    return (
        <div className={`Toc-Rows Toc-${align} ${active ? "Fade-Object-Active":"Fade-Object-Inactive"}`}>
            {objects.map((obj, i) => {
                if(i < start || i > end){
                } else {
                    return (
                    <div onMouseEnter={mouseEnter} onMouseLeave={mouseLeave} onMouseDown={handleMouseDown} data-index={obj.index}>
                        <span>{obj.pages}</span>
                        <h4>{obj.topic}</h4>
                        <p>{obj.author}</p>
                        <b className='Line-Target'></b>
                    </div>
                    )
                }
            })}
        </div>
    );
}

export default TocRows;