import ThreeManager from "./ThreeManager";
import * as THREE from "three";

class CanvasManager{
    constructor(id, reactFunctions){
        this.id = id;
        this.w = window.innerWidth;
        this.h = window.innerHeight;
        this.reactFunctions = reactFunctions;

        this.canvas = null;
        this.threeManager = null;
        
        this.init();
    }

    init(){
        this.canvas = document.querySelector(this.id);

        this.canvas.width = this.w;
        this.canvas.height = this.h;

        this.threeManager = new ThreeManager(this.canvas, this.reactFunctions, this);

        window.addEventListener("resize", (e) => {
            this.w = window.innerWidth;
            this.h = window.innerHeight;

            this.threeManager.camera.aspect = this.w/this.h;
            this.threeManager.camera.updateProjectionMatrix();

            this.threeManager.renderer.setSize(this.w,this.h);

        })
    }

    click(e){
        this.threeManager.select(e,this.w,this.h);
        
        this.threeManager.objects.forEach((obj,i) => {
            console.log(obj.custom.constraint);
            obj.custom.constraint.maxForce = -1;
            obj.custom.constraint.update();

        })

    }

    setTitleActive(state){
        this.threeManager.setTitleActive(state);
    }
}

export default CanvasManager;