
import * as CANNON from "cannon-es";
import { clamp } from "../utils";

class CannonManager{
    constructor(){
        this.world = new CANNON.World();
        this.bodies = [];

        this.attractor = {
           x: 0,
           y: 2,
           z: 0,
           strength: 23, 
        }

        this.constraint = {
            x: -3,
            y: 2,
            z: 3,
            vec: new CANNON.Vec3(this.x,this.y,this.z),
            vec2: new CANNON.Vec3(this.x,this.y,this.z),
            body: new CANNON.Body({position: new CANNON.Vec3(this.x, this.y, this.z)})
        }

        this.init();
    }

    init(){

        this.world.gravity.set(0,0,0);

        // let cubeBody = new CANNON.Body({
        //     mass: 5
        // })
        // let cubeShape = new CANNON.Box(new CANNON.Vec3(1,1,1));
        // cubeBody.addShape(cubeShape);
        // cubeBody.position.set(0,10,0);
        
        // this.bodies.push(cubeBody);

    }

    impulse(body){

        let a = this.attractor;
        let b = body;

        // let x = 10/(a.x-b.position.x);
        // let y = 10/(a.y-b.position.y);
        // let z = 10/(a.z-b.position.z);
        let x = (a.x-b.position.x)/10;
        let y = (a.y-b.position.y)/10;
        let z = (a.z-b.position.z)/10;

    

        x = clamp(x,-4,4);
        y = clamp(y,-4,4);
        z = clamp(z,-4,4);

        let force = new CANNON.Vec3(x,y,z);
        force = force.normalize();


        // let force = new CANNON.Vec3((1)*40,0,0);
        // body.applyForce(force);
        body.applyForce(force);
        body.applyTorque(new CANNON.Vec3(0,0.1,0));
    }
}

export default CannonManager;